import React, { useState } from 'react'
import {Form} from 'react-bootstrap'
import styles2 from '../../css/COLUMN.module.css'
import ElemDeleteControl from "../ElemDeleteControlWrapper"
import PrepareCSSClass from '../../../components/IFormDesignerLayout/PrepareCSSClass'
import { useSelector } from "../../../Redux";
import { GetState, GetStateFromData } from '../../../components/CustomHooks/CustomHooks'
import { decryptMessage, encrypt } from '../../../components/IFormDesignerLayout/IFormDesignerLogic'

function CustomControl(props) { 
    const isSubformInPage = props?.isSubformInPage || false;

    const contexts = GetStateFromData(props.containerId);

      const {controlSelected} = GetState("controlSelected");

      const {formState2, deleteControl}=contexts.contexts;
    // 
    var themeData;
    if(contexts!==undefined){
        themeData = formState2.theme ;
        
    }
    const data = props.data; 
    


    
    var jsonForCustomControlLabel = {'font-size' : '12px', "font-family":"Open Sans", 'color':'#000000'};
    if(themeData && themeData['BasicTheme'] && themeData['BasicTheme']['FontFamily']){
        jsonForCustomControlLabel['font-family'] = themeData['BasicTheme']['FontFamily'] ;
      }
      if(themeData && themeData['BasicTheme'] && themeData['BasicTheme']['FontSize']){
        jsonForCustomControlLabel['font-size'] = themeData['BasicTheme']['FontSize'] ;
      }
      if(themeData && themeData['BasicTheme'] && themeData['BasicTheme']['FontColor']){
        jsonForCustomControlLabel['color']  = themeData['BasicTheme']['FontColor'] ;
      }

    if(props.previewmode ){
        
        return (
            data?.generateHTML ?
            <div dangerouslySetInnerHTML={{ __html: decryptMessage(data?.generateHTML)}}></div> :
            <Form.Group 
            className={styles2.formStyle} key={data.id} 
             control_id={data.id} elem_type= {data.elem_type} >
                 <PrepareCSSClass  stylingData={{'customControllabel' : jsonForCustomControlLabel}}/>
                <Form.Label 
                    id_label={data.id} 
                    control_id={props.id} 
                    elem_type='toggle' 
                    frameindex={data.frameIndex} 
                    elementindex={data.elementIndex} 
                    className={['customControllabel'].join(" ")}
                >
                    {data && data.controlName ? 
                        data.controlName
                    :
                        data.id
                    }
                </Form.Label>
                
            </Form.Group>
       
       )
    } else if(props.data ){
        return (
            <Form.Group  
                // onClick={(e)=>props.handleSelected(e,data.id,"n")} 
                onClick={isSubformInPage ? {} : (e)=>props.handleSelected(e,"","n")}
            onKeyDown={(e) => props.handleSelected(e)}
            
                className={styles2.formStyle} 
                key={data.id} 
                id={data.id}
                control_id={data.id}  
                tabIndex="1"
                elem_type={data.elem_type}  
                // style={{backgroundColor: "red"}}
            >
                <PrepareCSSClass  stylingData={{'customControllabel' : jsonForCustomControlLabel}}/>
                <Form.Label 
                    id_label={data.id} 
                    control_id={props.id} 
                    elem_type='customControl' 
                    frameindex={data.frameIndex} 
                    elementindex={data.elementIndex} 
                    className={['customControllabel'].join(" ")}
                >
                    {data && data.controlName ?
                        data.controlName
                    :
                        data.id
                    }
                </Form.Label>
                {controlSelected?.id === props?.id &&
                <ElemDeleteControl 
                    isSubformInPage={isSubformInPage}
                    containerId={props.containerId}
                    id={props.data.id} 
                    data={props.data}
                    deleteControl={deleteControl}
                    handleReorderStart={props.handleReorderStart}
                    handleReorderDrop={props.handleReorderDrop} 
                    elemType={props.data.elem_type}
                    setDragging={props.setDragging}
                    dragId={props.dragId}
                    dragType={'column'}
                /> 
    }
            </Form.Group>
       
            
       )
    }

}
 

export default CustomControl ;